ul {
  background-color: #333;
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

li {
  float: left;
}

li a {
  color: white;
  display: block;
  padding: 14px 16px;
  text-align: center;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #111;
}
/* For about */
.avatar {
  border-radius: 50%;
  width: 100px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

/* For description */
.description {
  display: -webkit-flex;
  display: flex;
}

.intro-avatar {
  margin: auto;
}

.intro-description {
  padding: 10px;
}

.hello-words {
  text-align: center;
}

/* For experience */
.job {
  display: -webkit-flex;
  display: flex;
}

.company-logo {
  padding: 10px;
}
